.App {
  display: flex;
  justify-content: center;
  margin-top: 25vh;
}

.drum-pads {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.drum-pad {
  border: 1px solid black;
  margin: 10px;
  border-radius: 5px;
  width: 150px;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 5px 5px 5px 2px lightgray;
}